
import React, { Component } from 'react'

class NoUser extends Component {
  render() {
    return (
      <div className="No-user">
        <h3>Go to <a href="https://sekaru.github.io/Octopocto/#sekaru">sekaru.github.io/Octopocto/#[username]</a> to view a generated portfolio!</h3>
      </div>
    );
  }
}

export default NoUser



